<template>
  <div>
    <div class="container">
      <intro>
        <template slot="text">
          <p>Наша компания работает для активных и ответственных людей, ценящих свое время, желающих зарабатывать и умеющих тратить,
            идущих к своей цели, но стремящихся жить здесь и сейчас.</p>
          <p>Мы оперативно решаем вопрос предоставления займа, не требуем залогов и лишних документов, предлагаем прозрачные индивидуальные
            условия сотрудничества. Наши средства максимально доступны для предпринимателей с четким планом действий, а также
            для тех, кто умеет «держать слово».</p>
        </template>
      </intro>
      <steps></steps>
    </div>
    <sector>
      <subtitle>Как получить займ?</subtitle>
      <help-block
        desc="Наша компания выдает займы наличными в г. Саратов"
        legend="Инструкция по получению займа"
        img="loan-instruction.png"
      >
        <li>Убедитесь, что Вам больше 18 лет и Вы находитесь в г. Саратов</li>
        <li>Заполните <router-link to="/application-form">анкету</router-link> на нашем сайте</li>
        <li>Дождитесь СМС с одобрением займа</li>
        <li>Возьмите с собой паспорт и получите деньги наличными у нас в офисе: г. Саратов, ул. Политехническая, о. 103</li>
      </help-block>

      <subtitle>Как погасить займ?</subtitle>
      <help-block
        desc="Узнать свою задолженность Вы можете в <a href='/application-form'>личном кабинете</a> нашего сайта. Для этого нужно ввести Ваш телефон и код, который мы пришлем Вам по СМС."
        legend="Погасить займ можно несколькими способами:"
        img="account-instruction.png"
      >
        <li>С помощью банковской карты</li>
        <li>Наличными в офисе</li>
      </help-block>
    </sector>
    <sector>
      <subtitle>Ответы на часто задаваемые вопросы</subtitle>
      <faq></faq>
    </sector>
    <sector mods="dark">
      <subtitle>Документы МФО</subtitle>
      <docs></docs>
    </sector>
    <sector mods="dark">
      <subtitle>Реквизиты МФО</subtitle>
      <requisites></requisites>
    </sector>
  </div>
</template>

<script>
export default {
  methods: {
    log ({ target: { tagName, className } }) {
      console.log(tagName, className)
    }
  }
}
</script>



