import { uid } from '../utils'

export default {
  inheritAttrs: false,
  inject: ['Field'],
  props: {
    autofocus: Boolean,
    disabled: Boolean,
    id: {
      type: String,
      default: () => uid()
    },
    name: String,
    placeholder: String,
    required: Boolean,
    value: {}
  },
  data () {
    return {
      localValue: this.value,
      isFocused: false
    }
  },
  computed: {
    classes () {
      return {
        'input_focused': this.isFocused,
        'input_dirty': this.isDirty,
        'input_error': this.hasError,
        'input_valid': this.isValid
      }
    },
    attributes () {
      return {
        ...this.$attrs,
        class: this.classes,
        disabled: this.disabled,
        id: this.id,
        name: this.name,
        required: this.required
      }
    }
  },
  watch: {
    value (value) {
      this.localValue = value
    },
    localValue (value) {
      this.$emit('input', value)
    }
  },
  methods: {
    onInput (event) {
      this.localValue = event.target.value
    },
    onChange (event) {
      this.$emit('change', event)
    },
    onFocus (event) {
      this.isFocused = true
      this.$emit('focus', event)
    },
    onBlur (event) {
      this.isFocused = false
      this.$emit('blur', event)
    },
    focus () {
      this.$refs.input.focus()
    },
    blur () {
      this.$refs.input.blur()
    },
    shareInputData () {
      this.$watch('id', (value) => {
        this.Field.id = value
      }, { immediate: true })

      this.$watch('isFocused', (value) => {
        this.Field.isFocused = value
      }, { immediate: true })

      this.$watch('isDirty', (value) => {
        this.Field.isDirty = value
      }, { immediate: true })

      this.$watch('disabled', (value) => {
        this.Field.disabled = value
      }, { immediate: true })

      this.$watch('required', (value) => {
        this.Field.required = value
      }, { immediate: true })

      this.$watch('localValue', (value) => {
        this.Field.value = value
      }, { immediate: true })
    }
  },
  created () {
    this.shareInputData()
  },
  mounted () {
    if (this.autofocus) {
      this.$nextTick().then(this.focus)
    }
  }
}



