<template>
  <div class="calculator" v-cloak>
    <div
      :is="standalone ? 'form' : 'div'"
      @submit.prevent="onSubmit"
      class="calculator__form"
    >
      <div class="calculator__calculation">
        <div class="calculator__controls">
          <div class="calculator__control">
            <div class="calculator__control-label">мне нужно</div>
            <div class="calculator__control-value">{{ controls.amount.value | formatNumber }} <span class="calculator__control-unit">₽</span></div>
            <range
              name="amount"
              :min="controls.amount.min"
              :max="controls.amount.max"
              :step="controls.amount.step"
              v-model="controls.amount.value"
              :slider-options="controls.amount.options"
            ></range>
          </div>
          <div class="calculator__control">
            <div class="calculator__control-label">сроком на</div>
            <div class="calculator__control-value">{{ controls.days.value | formatNumber }} <span class="calculator__control-unit">дн</span></div>
            <range
              name="days"
              :min="controls.days.min"
              :max="controls.days.max"
              :step="controls.days.step"
              v-model="controls.days.value"
            ></range>
          </div>
        </div>
        <div class="calculator__clauses">
          <div class="calculator__clause">
            <div class="calculator__clause-label">сумма займа</div>
            <div class="calculator__clause-value">{{ controls.amount.value | formatNumber }} <span class="calculator__clause-unit">₽</span></div>
          </div>
          <div class="calculator__sep"></div>
          <div class="calculator__clause">
            <div class="calculator__clause-label">ставка</div>
            <div class="calculator__clause-value">{{ interestRate }}% <span class="calculator__clause-unit">в д</span></div>
          </div>
          <div class="calculator__sep"></div>
          <div class="calculator__clause">
            <div class="calculator__clause-label">процент</div>
            <div class="calculator__clause-value">{{ totalInterest | formatNumber }} <span class="calculator__clause-unit">₽</span></div>
          </div>
          <div class="calculator__sep"></div>
          <div class="calculator__clause">
            <div class="calculator__clause-label">к возврату</div>
            <div class="calculator__clause-value">{{ totalPayment | formatNumber }} <span class="calculator__clause-unit">₽</span></div>
          </div>
          <div class="calculator__sep"></div>
          <div class="calculator__clause">
            <div class="calculator__clause-label">дата возврата</div>
            <div class="calculator__clause-value">{{ repaymentDate }}</div>
          </div>
        </div>
      </div>
      <div v-if="standalone" class="calculator__completion">
        <div class="calculator__submit-container">
          <btn variant="primary" type="submit" class="calculator__submit">ПОЛУЧИТЬ ЗАЙМ</btn>
          <span class="calculator__note">Рассмотрим заявку за 10 минут</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    standalone: {
      type: Boolean,
      default: true
    }
  },
  data () {
    return {
      controls: {
        amount: {
          min: 1000,
          max: 30000,
          step: 100,
          value: 24000,
          options: {}
        },
        days: {
          min: 5,
          max: 30,
          step: 1,
          value: 10
        }
      },
      interestRate: 2
    }
  },
  computed: {
    totalInterest () {
      return this.controls.amount.value * (this.interestRate / 100) * this.controls.days.value
    },
    totalPayment () {
      return this.controls.amount.value + this.totalInterest
    },
    repaymentDate () {
      let date = new Date()
      let monthDay, monthIndex
      let months = ['января', 'февраля', 'марта', 'апреля', 'мая', 'июня',
        'июля', 'августа', 'сентября', 'октября', 'ноября', 'декабря']
      date.setDate(date.getDate() + this.controls.days.value)
      monthDay = date.getDate()
      monthIndex = date.getMonth()

      return `${monthDay} ${months[monthIndex]}`
    }
  },
  methods: {
    onSubmit (e) {
      this.$router.push('/application-form')
    }
  },
  filters: {
    formatNumber (num) {
      return num.toString().replace(/(?!^)(\d{3})(?=(?:\d{3})*$)/g, ' $1')
    }
  }
}
</script>

<style lang="scss">
//$calculator-clause-bp: 576px;

.calculator {
  &__form {
    display: flex;
    flex-wrap: wrap;
    margin: 0 $grid-gutter / -2;
  }

  &__calculation {
    flex: 1 1 auto;
    //width: 67%;
    padding: 0 $grid-gutter / 2;

    @include below(lg) {
      width: 100%;
    }
  }

  &__controls {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin: 0 $grid-gutter / -2 20px;
  }

  &__control {
    flex: 0 1 auto;
    //width: 270px;
    //min-width: 46%;
    width: 310px;
    min-width: 41%;
    padding: 0 $grid-gutter / 2;

    @include below(md) {
      width: 100%;

      & + & {
        margin-top: 25px;
      }
    }
  }

  &__control-label {
    color: color(gray-light);
    font-size: 14px;
    line-height: 1.2;
    margin-bottom: 8px;
  }

  &__control-value {
    color: color(gray-dark);
    font-size: 24px;
    font-weight: 700;
    line-height: 1;
    margin-bottom: 19px;
  }

  &__control-unit {
    font-size: 18px;
  }

  input[type='range'] {
    width: 100%;
  }

  &__clauses {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    @include below(md) {
      justify-content: center;
      margin: 0 -56px;
    }
  }

  &__clause {
    flex: 0 1 auto;
    margin: 8px 0;

    @include below(md) {
      margin-left: 56px;
      margin-right: 56px;
    }
  }

  &__clause-label {
    color: color(gray-light);
    font-size: 14px;
    line-height: 1.2;
    margin-bottom: 5px;
  }

  &__clause-value {
    color: #000000;
    font-size: 16px;
    font-weight: 500;
    line-height: 1.2;
  }

  &__clause-unit {
    font-size: 12px;
  }

  &__sep {
    flex: none;
    //align-self: center;
    //height: 42px;
    background-color: #979797;
    //opacity: 0.09;
    opacity: 0.15;
    //background: red;
    margin: 8px 0;
    width: 1px;

    &:nth-child(6n) {
      @include between(sm, md) {
        height: 1px;
        width: 100%;
      }
    }

    &:nth-child(4n) {
      @include below(sm) {
        height: 1px;
        width: 100%;
      }
    }

    //&:nth-child(6n) {
    //    width: calc((#{$calculator-clause-bp} - 100%) * #{strip-unit($calculator-clause-bp) * 10});
    //    min-width: 1px;
    //    max-width: 100%;
    //    min-height: 1px;
    //}
  }

  &__completion {
    flex: 0 0 auto;
    width: 33%;
    padding: 0 $grid-gutter / 2;
    text-align: right;
    margin-top: 15px;

    @include below(lg) {
      width: 100%;
      text-align: center;
      margin-top: 40px;
    }
  }

  &__submit-container {
    display: inline-flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
  }

  &__submit {}

  &__note {
    color: color(gray-light);
    font-size: 14px;
    margin-top: 16px;
    display: inline-block;
  }
}
</style>



