import Vue from 'vue'
import store from './store'
import router from './router'
import App from './App.vue'
import PortalVue from 'portal-vue'
import VeeValidate, { Validator } from 'vee-validate'
import './helpers/vee-validate-locale-ru'
import './helpers/vee-validate-custom-rules'
import * as components from './components'
import * as directives from './directives'

Vue.config.productionTip = process.env.NODE_ENV === 'production'

// PortalVue
Vue.use(PortalVue)

// VeeValidate
const validateAll = Validator.prototype.validateAll
Validator.prototype.validateAll = function (scope) {
  return validateAll.apply(this, arguments).then((result) => {
    if (!result) {
      const fields = this.fields.items.filter((field) => {
        return (
          // eslint-disable-next-line
          !field.component.disabled && field.scope == scope &&
          field.flags.validated && !field.flags.valid
        )
      })
      const field = fields[0]
      const component = field.component
      if (component && component.blur && component.focus) {
        component.blur()
        requestAnimationFrame(() => component.focus())
      }
      console.log('PATCHING', fields, field)
    }
    return result
  }, (error) => {
    throw error
  })
}
Vue.use(VeeValidate, { inject: false, aria: false })

// Components
Object.keys(components).forEach((key) => {
  Vue.component(key, components[key])
})

// Directives
Object.keys(directives).forEach((key) => {
  Vue.directive(key, directives[key])
})

/* eslint-disable no-new */
new Vue({
  el: '#app',
  store,
  router,
  render: h => h(App)
})



