import { addClass, removeClass } from '../utils/dom'

const KEY = '__v-toggle__'

export default {
  bind (el, binding, vnode) {
    const ids = Object.keys(binding.modifiers || {})

    if (ids.length === 0) {
      return
    }

    const collapsedClass = binding.value ? binding.value + '' : 'collapsed'

    el.addEventListener(binding.arg || 'click', (event) => {
      event.preventDefault()
      ids.forEach(id => {
        vnode.context.$root.$emit('toggle::collapse', id)
      })
    })

    el[KEY] = (id, visible) => {
      if (ids.indexOf(id) > -1) {
        if (visible) {
          removeClass(el, collapsedClass)
        } else {
          addClass(el, collapsedClass)
        }
      }
    }

    vnode.context.$root.$on('collapse::change', el[KEY])
  },
  unbind (el, binding, vnode) {
    if (el[KEY]) {
      vnode.context.$root.$off('collapse::change', el[KEY])
      el[KEY] = null
    }
  }
}



