<!--<template functional>-->
  <!--<h2 :class="['subtitle', data]" v-bind="data.attrs" v-on="data.on">-->
    <!--<pre>{{ children }}</pre>-->
    <!--<slot></slot>-->
  <!--</h2>-->
<!--</template>-->

<script>
export default {
  functional: true,
  props: {
    level: {
      type: Number,
      default: 1
    }
  },
  render (h, { props, data, children }) {
    const Tag = `h${props.level}`
    return (
      <Tag class="subtitle" {...data}>
        {children}
      </Tag>
    )
  }
}
</script>

<style lang="scss">
.subtitle {
  max-width: 600px;
  color: color(gray-dark);
  font-size: 28px;
  font-weight: 400;
  line-height: 1.2;
  text-align: center;
  margin: 89px auto 39px;

  &:first-child {
    margin-top: 0;
  }
}
</style>



