<!--<template>-->
  <!--<div class="help-block">-->
    <!--<div class="help-block__content">-->
      <!--<div class="help-block__detail">-->
        <!--<div class="help-block__desc" v-html="desc"></div>-->
        <!--<div class="help-block__legend" v-html="legend"></div>-->
        <!--<ul class="help-block__list">-->
          <!--<slot></slot>-->
        <!--</ul>-->
      <!--</div>-->
      <!--<div class="help-block__image">-->
        <!--<img class="help-block__img" :src="require(`../assets/img/${img}`)"/>-->
      <!--</div>-->
    <!--</div>-->
  <!--</div>-->
<!--</template>-->

<script>
export default {
  functional: true,
  props: {
    desc: {
      type: String
    },
    legend: {
      type: String
    },
    img: {
      type: String
    },
    list: {
      type: Array,
      default: () => []
    }
  },
  render (h, { props, data, children }) {
    return (
      <div class="help-block" {...data}>
        <div class="help-block__content">
          <div class="help-block__detail">
            <div class="help-block__desc" domPropsInnerHTML={props.desc}></div>
            <div class="help-block__legend" domPropsInnerHTML={props.legend}></div>
            <ul class="help-block__list">
              {children}
            </ul>
          </div>
          <div class="help-block__image">
            <img class="help-block__img" src={require(`../assets/img/${props.img}`)}/>
          </div>
        </div>
      </div>
    )
  }
}
</script>

<style lang="scss">
$help-block-grid-gutter: 20px;

.help-block {

  a {
    color: #79ce58;
    text-decoration: none;

    &:hover {
      text-decoration: underline;
    }
  }

  &__title {}

  &__content {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0 $help-block-grid-gutter / -2;

    @include below(md) {
      flex-direction: column;
    }
  }

  &__detail {
    flex: 0 1 auto;
    padding: 0 $help-block-grid-gutter / 2;
    width: 375px;
    max-width: 100%;

    @include below(md) {
      margin-bottom: 20px;
    }
  }

  &__desc {
    color: #292929;
    font-size: 16px;
    line-height: 23px;
    margin-bottom: 35px;
  }

  &__legend {
    color: color(gray-light);
    font-size: 16px;
    line-height: 23px;
    margin-bottom: 14px;
  }

  &__list {
    counter-reset: li-counter;
  }

  li,
  &__item {
    margin-bottom: 20px;
    display: flex;

    &:before {
      content: counter(li-counter);
      counter-increment: li-counter;
      width: 20px;
      height: 20px;
      line-height: 20px;
      background-color: #7bbe60;
      color: #ffffff;
      text-align: center;
      font-size: 12px;
      font-weight: 500;
      display: inline-block;
      flex: none;
      margin-right: 10px;
      border-radius: 50%;
    }
  }

  &__image {
    flex: 0 1 auto;
  }

  &__img {
    width: 451px;
  }
}
</style>



