<template>
  <div>
    <modal
      id="auth-phone"
      v-model="visibility.phone"
      size="medium"
    >
      <div class="auth">
        <div class="auth__title">Для входа в личный кабинет введите Ваш телефон</div>
        <form novalidate class="auth__form" @submit.prevent="onSubmit('phone')">
          <div class="auth__row">
            <div class="auth__field">
              <form-field label="Номер телефона">
                <form-input
                  autofocus
                  name="phone"
                  v-model="phone.value"
                  mask="+7 (999) 999-99-99"
                  v-validate="'required|phone'"
                  ref="phone"
                ></form-input>
              </form-field>
            </div>
          </div>
          <div class="auth__row">
            <div class="auth__field">
              <form-field label="Сообщение">
                <form-input
                  type="textarea"
                  name="comment"
                  v-model="comment"
                  v-validate="'required|min:5'"
                  data-vv-scope="area"
                ></form-input>
              </form-field>
            </div>
          </div>
          <div class="auth__row">
            <form-field>
              <form-checkboxes>
                <form-checkbox
                  v-for="(value, index) in ['Vue', 'Node', 'JS']"
                  :key="index"
                  name="checked[]"
                  :label="value"
                  v-model="checked"
                  :value="index"
                  v-validate="index === 0 ? 'required' : ''"
                ></form-checkbox>
              </form-checkboxes>
            </form-field>
          </div>
          <div class="auth__footer">
            <btn variant="primary" type="submit" class="auth__button">ВОЙТИ</btn>
            <div class="auth__note">Вам придет СМС с кодом для входа</div>
          </div>
        </form>
      </div>
    </modal>
    <modal
      id="auth-code"
      v-model="visibility.code"
      size="medium"
      @shown="startTimer"
      @hidden="destroyTimer"
    >
      <div class="auth">
        <div class="auth__title">Мы отправили код на номер<br/>+7 911 333 23 23</div>
        <form novalidate class="auth__form" @submit.prevent="onSubmit('code')">
          <div class="auth__row auth__row_countdown">
            <div class="auth__field">
              <form-field label="Код из смс">
                <form-input
                  autofocus
                  name="code"
                  v-model="code.value"
                  mask="9999"
                  v-validate="'required|sms_code'"
                  ref="code"
                ></form-input>
              </form-field>
            </div>
            <div class="auth__countdown">{{ timer | formatTime }}</div>
          </div>
          <div class="auth__footer">
            <btn variant="primary" type="submit" class="auth__button">ВОЙТИ</btn>
            <a
              href="#"
              class="auth__note"
              :class="{ 'auth__note_disabled': timer !== 0 }"
              @click.prevent="startTimer()"
            >Отправить код ещё раз</a>
          </div>
        </form>
      </div>
    </modal>
    <modal
      id="auth-account"
      v-model="visibility.account"
      size="large"
    >
      <account></account>
    </modal>
  </div>
</template>

<script>
export default {
  name: 'auth',
  $_veeValidate: {
    validator: 'new'
  },
  data () {
    return {
      test: '',
      comment: '',

      step: 'phone', // phone, code, account
      phone: {
        value: ''
      },
      code: {
        value: ''
      },
      visibility: {
        phone: false,
        code: false,
        account: false
      },
      timer: 60 * 1000,
      intervalID: null,
      // showHelper: true,

      checked: []
    }
  },
  computed: {
    visible: {
      get () {
        let { phone, code, account } = this.visibility
        return phone || code || account
      },
      set (value) {
        this.visibility[this.step] = value
      }
    }
  },
  watch: {
    step (newVal, oldVal) {
      this.visibility[oldVal] = false
      this.visibility[newVal] = true
    }
  },
  methods: {
    async onSubmit (step) {
      const result = await this.$validator.validateAll()
      if (result) {
        if (this.step === 'phone') {
          this.step = 'code'
        } else if (this.step === 'code') {
          this.step = 'account'
        }
      }
    },
    show () {
      this.visible = true
    },
    hide () {
      this.visible = false
    },
    startTimer () {
      if (this.intervalID) {
        return
      }
      this.$validator.reset('code')
      this.timer = 60 * 1000
      this.intervalID = setInterval(() => {
        this.timer -= 1000
        if (this.timer === 0) {
          this.destroyTimer()
        }
      }, 1000)
    },
    destroyTimer () {
      clearInterval(this.intervalID)
      this.intervalID = null
    },
    authCodeOnShow () {
      this.startTimer()
    },
    authCodeOnHide () {
      this.destroyTimer()
    }
    // updateShowHelper() {
    //     this.showHelper = false;
    //     this.$nextTick(() => {
    //         this.showHelper = true;
    //     });
    // }
  },
  mounted () {
    this.$root.$on('show:auth', this.show)
  },
  beforeDestroy () {
    this.$root.$off('show:auth', this.show)
  },
  filters: {
    formatTime (val) {
      return new Date(val).toTimeString()
        .replace(/.*\d{2}:(\d{2}:\d{2}).*/, '$1')
    }
  }
}
</script>

<style lang="scss">
.auth {
  &__title {
    color: #000000;
    font-size: 18px;
    font-weight: 400;
    line-height: 26px;
    text-align: center;
    margin-bottom: 46px;
    width: 250px;
    margin-left: auto;
    margin-right: auto;
  }

  &__form {}

  &__row {
    margin-bottom: 58px;

    &_countdown {
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }

  &__field {
    position: relative;

    //.form-error {
    //    position: absolute;
    //    top: 100%;
    //    left: 0;
    //    right: 0;
    //}
  }

  &__row_countdown &__field {
    flex: none;
    width: 169px;
  }

  &__countdown {
    flex: none;
    color: color(gray-light);
    font-size: 18px;
    margin-left: 13px;
    width: 50px;
  }

  &__footer {
    text-align: center;
    width: 250px;
    margin-left: auto;
    margin-right: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__button {}

  &__note {
    display: inline-block;
    margin-top: 14px;
    color: color(gray-light);
    font-size: 14px;
    font-weight: 400;

    @at-root a#{&} {
      text-decoration: none;

      &:hover {
        text-decoration: underline;
      }
    }

    &_disabled {
      visibility: hidden;
    }
  }
}
</style>



