export default {
  computed: {
    shouldValidate () {
      const { directives } = this.$vnode.data
      return !!(directives && directives
        .filter(x => x.name === 'validate').length)
    },
    scope () {
      if (!this.shouldValidate) {
        return
      }
      return this.$attrs['data-vv-scope']
    },
    computedName () {
      if (!this.shouldValidate) {
        return
      }
      return this.name || this.$attrs['data-vv-name']
    },
    scopedName () {
      if (!this.shouldValidate) {
        return
      }
      const { computedName, scope } = this
      return scope ? scope + '.' + computedName : computedName
    },
    errorMessage () {
      if (!this.shouldValidate) {
        return
      }
      const { errors } = this.$vnode.context
      return errors && errors.first(this.scopedName)
    },
    hasError () {
      return this.flags &&
                this.flags.validated &&
                this.flags.invalid
    },
    isValid () {
      return this.flags &&
                this.flags.validated &&
                this.flags.valid
    },
    flags () {
      if (!this.shouldValidate) {
        return
      }
      const { fields } = this.$vnode.context
      if (fields) {
        const $scope = this.scope ? fields['$' + this.scope] : fields
        return $scope && $scope[this.computedName]
      }
    }
  },
  methods: {
    validate () {
      if (!this.shouldValidate) {
        return
      }
      const { $validator } = this.$vnode.context
      return $validator && $validator.validate(this.scopedName)
    },
    shareValidationData () {
      this.$watch('errorMessage', (value) => {
        this.Field.errorMessage = value
      }, { immediate: true })

      this.$watch('hasError', (value) => {
        this.Field.hasError = value
      }, { immediate: true })

      this.$watch('isValid', (value) => {
        this.Field.isValid = value
      }, { immediate: true })
    },
    customMessages (messages) {
      if (!this.shouldValidate) {
        return
      }
      const { $validator } = this.$vnode.context
      $validator && $validator.localize($validator.locale, {
        custom: { [this.computedName]: messages }
      })
    }
  },
  created () {
    if (this.Field && this.shouldValidate) {
      this.shareValidationData()
    }
  },
  beforeDestroy () {
    const { errors } = this.$vnode.context
    errors && this.$nextTick(() => {
      errors.remove(this.computedName, this.scope)
    })
  }
}



