<template>
  <div class="contacts-inline">
    <div class="contacts-inline__item u-hidden-xl-down">
      <portal to="address-contact" :disabled="$mq.xlAndUp" slim>
        <contact icon="house" mods="address">г. Саратов, ул. Политехническая, о. 103</contact>
      </portal>
    </div>
    <div class="contacts-inline__item u-hidden-md-down">
      <portal to="phone-contact" :disabled="$mq.mdAndUp" slim>
        <contact icon="handset" mods="phone">8 800 434 33 33</contact>
      </portal>
    </div>
  </div>
</template>

<script>
import mediaQueryMixin from '@/mixins/media-query'

export default {
  mixins: [mediaQueryMixin],
  data () {
    return {}
  }
}
</script>

<style lang="scss">
.contacts-inline {
  display: flex;
  align-items: center;
  justify-content: center;
  flex: 0 1 auto;

  &__item {
    flex: 0 1 auto;
    margin: 0 18px;

    @include below(xl) {
      margin: 0;
    }
  }
}
</style>



