<template>
  <div :class="classes">
    <icon :name="icon" class="contact__icon"></icon>
    <span class="contact__value">
      <slot></slot>
    </span>
  </div>
</template>

<script>
import bemMixin from '@/mixins/bem'

export default {
  block: 'contact',
  mixins: [bemMixin],
  props: {
    icon: {
      type: String,
      require: true
    }
  }
}
</script>

<style lang="scss">
.contact {
  align-items: baseline;
  display: inline-flex;

  &__icon {
    display: inline-block;
    vertical-align: middle;
    margin-right: 9px;
    color: color(primary);
    position: relative;
    top: 2px;
    flex: none;
  }

  &_address &__icon {
    font-size: 13px;
  }

  &_phone &__icon {
    font-size: 15px;
  }

  &__value {
    display: inline-block;
    vertical-align: middle;
    color: #292929;
    font-size: 14px;
    flex: 0 1 auto;
  }
}
</style>



