<template>
  <div v-if="!isHidden">
    <div
      :id="id"
      :class="modalClasses"
      ref="modal"
      v-show="isVisible || isTransitioning"
      @click="onClickOut"
      @keyup.esc="onEsc"
    >
      <transition
        enter-class="modal__dialog_out"
        enter-to-class=""
        enter-active-class=""
        leave-class=""
        leave-to-class="modal__dialog_out"
        leave-active-class=""
        @before-enter="onBeforeEnter"
        @enter="onEnter"
        @after-enter="onAfterEnter"
        @before-leave="onBeforeLeave"
        @leave="onLeave"
        @after-leave="onAfterLeave"
      >
        <div class="modal__dialog" v-show="isVisible">
          <div class="modal__content" tabindex="-1" ref="content" @click.stop>
            <button class="modal__close" type="button" @click="hide()">
              <icon name="cross" class="modal__close-icon"></icon>
            </button>
            <slot></slot>
          </div>
        </div>
      </transition>
    </div>
    <backdrop :visible="backdropVisible" @click="onClickOut"></backdrop>
  </div>
</template>

<script>
import popupMixin from '@/mixins/popup'

export default {
  mixins: [popupMixin],
  props: {
    id: String,
    size: String
  },
  data () {
    return {
      eventKey: 'modal'
    }
  },
  computed: {
    modalClasses () {
      return [
        'modal',
        {
          [`modal_${this.size}`]: !!this.size
        }
      ]
    }
  },
  methods: {
    onEnter () {
      popupMixin.methods.onEnter.call(this)
      this.$refs.modal.scrollTop = 0
    },
    showHandler (id) {
      if (id === this.id) {
        this.show()
      }
    },
    hideHandler (id) {
      if (id === this.id) {
        this.hide()
      }
    },
    modalListener (e) {
      if (e.vueTarget !== this) {
        this.hide()
      }
    }
  },
  mounted () {
    this.$root.$on('show:modal', this.showHandler)
    this.$root.$on('hide:modal', this.hideHandler)
    this.$root.$on('modal:show', this.modalListener)
    if (this.visible) {
      this.show()
    }
  },
  beforeDestroy () {
    this.$root.$off('show:modal', this.showHandler)
    this.$root.$off('hide:modal', this.hideHandler)
    this.$root.$off('modal:show', this.modalListener)
  }
}
</script>

<style lang="scss">
$modal-transition: $popup-transition;

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  overflow-x: hidden;
  overflow-y: auto;
  z-index: z("modal");
  display: flex;
  padding: 30px 0;

  &__dialog {
    margin: auto;
    flex: 0 1 auto;
    max-width: 100%;
    transition: $modal-transition;
    transition-property: opacity, transform;
    will-change: opacity, transform;

    &_out {
      transform: scale(0.9, 0.85);
      opacity: 0;
    }
  }

  &__content {
    margin: auto;
    position: relative;
    background-color: #fff;
    max-width: 100%;
    padding: 30px;
  }

  &_medium &__content {
    width: 421px;
    padding: 74px 39px 65px;

    @include below(sm) {
      padding: 74px * 0.7 39px * 0.8 65px * 0.7;
    }
  }

  &_large &__content {
    width: 564px;
    padding: 80px 68px 83px;

    @include below(sm) {
      padding: 80px * 0.7 68px * 0.7 83px * 0.7;
    }
  }

  &__close {
    position: absolute;
    top: 0;
    right: 0;
    width: 58px;
    height: 58px;
    background-color: #f7f7f7;
    z-index: 100;
    transition: $transition;
    border-top-right-radius: inherit;
    cursor: pointer;

    &:hover {
      background-color: #f3f3f3;
    }
  }

  &__close-icon {
    color: #000;
    font-size: 22px;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    margin: auto;
  }
}
</style>



