<script>
// import { mergeData } from '@/utils'
const context = require.context('../assets/svg', true, /\.svg$/)
const icons = {}
context.keys().forEach((key) => {
  const { id, viewBox } = context(key).default
  const name = id.replace('icon-', '')
  const [width, height] = viewBox.split(' ').slice(-2).map(parseFloat)
  icons[name] = { id, ratio: width / height }
})

export default {
  functional: true,
  props: {
    name: {
      type: String
    }
  },
  render (h, { props, data }) {
    const { ratio } = icons[props.name]
    // const mergedData = mergeData(data, {
    //   staticClass: 'icon',
    //   attrs: {
    //     height: '1em',
    //     width: ratio + 'em'
    //   }
    // })
    return (
      <svg class="icon" height="1em" width={ratio + 'em'} {...data}>
        <use xlinkHref={`#icon-${props.name}`}></use>
      </svg>
    )
  }
}
</script>

<style>
.icon {
  display: inline-block;
  fill: currentColor;
  fill-rule: evenodd;
}
</style>



