<template>
  <transition
    enter-class="backdrop_out"
    enter-active-class=""
    enter-to-class=""
    leave-class=""
    leave-active-class=""
    leave-to-class="backdrop_out"
  >
    <div v-if="visible" :class="classes" v-on="$listeners"></div>
  </transition>
</template>

<script>
export default {
  props: {
    visible: {
      type: Boolean
    }
  },
  computed: {
    classes () {
      return [ 'backdrop' ]
    }
  }
}
</script>

<style lang="scss">
.backdrop {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: z("backdrop");
  background-color: #000;
  opacity: .5;
  transition: opacity $backdrop-transition;
  will-change: opacity;

  &_out {
    opacity: 0;
  }
}
</style>



