<template>
  <div class="application-form">
    <div class="container-narrow">
      <heading
        title="Заполните анкету для получения денег."
        subtitle="Наличными в Саратове."
      ></heading>
    </div>
    <form
      class="application-form__content"
      novalidate
      autocomplete="off"
      @submit.prevent="onSubmit"
    >
      <div class="application-form__header">
        <div class="application-form__container container-narrow">
          <calculator class="application-form__calculator" :standalone="false"></calculator>
        </div>
      </div>
      <div class="application-form__body">
        <div class="application-form__container container-narrow">
          <div class="application-form__group">
            <div class="application-form__group-inner">
              <div class="application-form__legend">ЛИЧНЫЕ ДАННЫЕ
              </div>
              <div class="application-form__row">
                <div class="application-form__col application-form__col_1-2">
                  <form-field label="Фамилия">
                    <form-input name="lastname" v-model="lastname" v-validate="'required|russian|capitalized|min:2'"></form-input>
                  </form-field>
                </div>
                <div class="application-form__col application-form__col_1-2">
                  <form-field label="Имя">
                    <form-input name="firstname" v-model="firstname" v-validate="'required|russian|capitalized|min:2'"></form-input>
                  </form-field>
                </div>
                <div class="application-form__col application-form__col_1-2">
                  <form-field label="Отчество">
                    <form-input name="middlename" v-model="middlename" v-validate="'required|russian|capitalized|min:2'"></form-input>
                  </form-field>
                </div>
                <div class="application-form__col application-form__col_1-2">
                  <form-field label="Дата рождения">
                    <form-input type="tel" name="birthday" v-model="birthday" :mask="{ alias: 'dd.mm.yyyy', placeholder: 'дд.мм.гггг' }" v-validate="'required|date_format:DD.MM.YYYY|age'"></form-input>
                  </form-field>
                </div>
                <div class="application-form__col application-form__col_1-2">
                  <form-field label="Электронная почта">
                    <form-input type="email" name="email" v-model="email" v-validate="'required|email'"></form-input>
                  </form-field>
                </div>
                <div class="application-form__col application-form__col_1-2">
                  <form-field label="Номер телефона">
                    <form-input type="tel" name="phone" v-model="phone" mask="+7 (999) 999-99-99" v-validate="'required|phone'"></form-input>
                  </form-field>
                </div>
              </div>
            </div>
          </div>
          <div class="application-form__group">
            <div class="application-form__group-inner">
              <div class="application-form__legend">ПАСПОРТНЫЕ ДАННЫЕ
              </div>
              <div class="application-form__row">
                <div class="application-form__col">
                  <form-field label="Серия и номер паспорта">
                    <form-input type="tel" name="passport-number" v-model="passportNumber" mask="99 99 999999" v-validate="'required|passport_number'"></form-input>
                  </form-field>
                </div>
                <div class="application-form__col application-form__col_1-2">
                  <form-field label="Дата выдачи">
                    <form-input type="tel" name="passport-date" v-model="passportDate" :mask="{ alias: 'dd.mm.yyyy', placeholder: 'дд.мм.гггг' }" v-validate="'required|date_format:DD.MM.YYYY|date_between:' + passportDateRange()"></form-input>
                  </form-field>
                </div>
                <div class="application-form__col application-form__col_1-2">
                  <form-field label="Код подразделения">
                    <form-input type="tel" name="passport-sub-сode" v-model="passportSubCode" mask="999-999" v-validate="'required|passport_sub_сode'" :readonly="false"></form-input>
                  </form-field>
                </div>
                <div class="application-form__col">
                  <form-field label="Кем выдан">
                    <form-input name="passport-issued-by" v-model="passportIssuedBy" v-validate="'required|russian|capitalized|min:2'"></form-input>
                  </form-field>
                </div>
              </div>
            </div>
          </div>
          <div class="application-form__group">
            <div class="application-form__group-inner">
              <div class="application-form__legend">АДРЕС ПРОПИСКИ
              </div>
              <div class="application-form__row">
                <div class="application-form__col application-form__col_1-2">
                  <form-field label="Область прописки">
                    <form-input name="reg-region" v-model="regRegion" v-validate="'required|russian|capitalized|min:2'"></form-input>
                  </form-field>
                </div>
                <div class="application-form__col application-form__col_1-2">
                  <form-field label="Город прописки">
                    <form-input name="reg-city" v-model="regCity" v-validate="'required|russian|capitalized|min:2'"></form-input>
                  </form-field>
                </div>
                <div class="application-form__col application-form__col_1-2">
                  <form-field label="Улица">
                    <form-input name="reg-street" v-model="regStreet" v-validate="'required|russian|min:2'"></form-input>
                  </form-field>
                </div>
                <div class="application-form__col application-form__col_1-2">
                  <div class="application-form__input-group">
                    <div class="application-form__cell application-form__cell_1-3">
                      <form-field label="Дом">
                        <form-input type="tel" name="reg-house" v-model="regHouse" v-validate="'required|min:1'"></form-input>
                      </form-field>
                    </div>
                    <div class="application-form__cell application-form__cell_1-3">
                      <form-field label="Строение">
                        <form-input type="tel" name="reg-building" v-validate="'min:1'" v-model="regBuilding"></form-input>
                      </form-field>
                    </div>
                    <div class="application-form__cell application-form__cell_1-3">
                      <form-field label="Квартира">
                        <form-input type="tel" name="reg-apartment" v-validate="'min:1'" v-model="regApartment"></form-input>
                      </form-field>
                    </div>
                  </div>
                </div>
              </div>
              <div class="application-form__bool">
                <form-checkbox name="address-same" label="Совпадает с адресом фактического проживания" v-model="addressSame"></form-checkbox>
              </div>
            </div>
          </div>
          <collapse class="application-form__group" :visible="!addressSame">
            <div class="application-form__group-inner">
              <div class="application-form__legend">ФАКТИЧЕСКИЙ АДРЕС ПРОЖИВАНИЯ
              </div>
              <div class="application-form__row">
                <div class="application-form__col application-form__col_1-2">
                  <form-field label="Область прописки">
                    <form-input name="act-region" v-model="actRegion" :disabled="addressSame" v-validate="{ rules: 'required|russian|capitalized|min:2', disabled: addressSame }"></form-input>
                  </form-field>
                </div>
                <div class="application-form__col application-form__col_1-2">
                  <form-field label="Город прописки">
                    <form-input name="act-city" v-model="actCity" :disabled="addressSame" v-validate="{ rules: 'required|russian|capitalized|min:2', disabled: addressSame }"></form-input>
                  </form-field>
                </div>
                <div class="application-form__col application-form__col_1-2">
                  <form-field label="Улица">
                    <form-input name="act-street" v-model="actStreet" :disabled="addressSame" v-validate="{ rules: 'required|russian|min:2', disabled: addressSame }"></form-input>
                  </form-field>
                </div>
                <div class="application-form__col application-form__col_1-2">
                  <div class="application-form__input-group">
                    <div class="application-form__cell application-form__cell_1-3">
                      <form-field label="Дом">
                        <form-input type="tel" name="act-house" v-model="actHouse" :disabled="addressSame" v-validate="{ rules: 'required|min:1', disabled: addressSame }"></form-input>
                      </form-field>
                    </div>
                    <div class="application-form__cell application-form__cell_1-3">
                      <form-field label="Строение">
                        <form-input type="tel" name="act-building" :disabled="addressSame" v-validate="{ rules: 'min:1', disabled: addressSame }" v-model="actBuilding"></form-input>
                      </form-field>
                    </div>
                    <div class="application-form__cell application-form__cell_1-3">
                      <form-field label="Квартира">
                        <form-input type="tel" name="act-apartment" :disabled="addressSame" v-validate="{ rules: 'min:1', disabled: addressSame }" v-model="actApartment"></form-input>
                      </form-field>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </collapse>
          <div class="application-form__footer">
            <div class="application-form__checks">
              <div class="application-form__check">
                <form-field>
                  <form-checkbox name="accept" label="Я соглашаюсь на обработку &lt;a href='#'&gt;персональных данных&lt;/a&gt;" v-model="accept" v-validate="'required'"></form-checkbox>
                </form-field>
              </div>
              <div class="application-form__check">
                <form-field>
                  <form-checkbox name="approval" label="Я принимаю &lt;a href='#'&gt;Соглашение об использовании Аналога собственноручной подписи&lt;/a&gt;" v-model="approval" v-validate="'required'"></form-checkbox>
                </form-field>
              </div>
            </div>
            <div class="application-form__button-area">
              <btn class="form__button" variant="primary" type="submit" :disabled="errors.any()">ПОЛУЧИТЬ ЗАЙМ</btn>
              <div class="application-form__note">Рассмотрим заявку за 10 минут
              </div>
              <modal id="Thanks-modal" size="large" ref="thanksModal">
                <thanks></thanks>
              </modal>
            </div>
          </div>
        </div>
      </div>
    </form>
  </div>
</template>

<script>
export default {
  $_veeValidate: {
    validator: 'new'
  },
  data () {
    return {
      lastname: '',
      firstname: '',
      middlename: '',
      birthday: '',
      email: '',
      phone: '',

      passportNumber: '',
      passportDate: '',
      passportSubCode: '',
      passportIssuedBy: '',

      regRegion: '',
      regCity: '',
      regStreet: '',
      regHouse: '',
      regBuilding: '',
      regApartment: '',

      actRegion: '',
      actCity: '',
      actStreet: '',
      actHouse: '',
      actBuilding: '',
      actApartment: '',

      addressSame: true,
      accept: false,
      approval: false,

      state: 1,
      accepting: [],
      acceptable: false,
      indeterminate: true,
      selected: null,
      options: [
        // { value: 'string', text: 'string' },
        // { value: '', text: '' },
        // { value: {'C': '3PO'}, text: '{"C": "3PO"}' },
        // { value: 'non empty value', text: '' }
      ]
    }
  },
  methods: {
    async onSubmit () {
      const result = await this.$validator.validateAll()
      if (result) {
        this.$refs.thanksModal.show()
      }
    },
    onChange (e) {},
    passportDateRange () {
      let now = new Date()
      let [dd, mm, yyyy] = [+now.getDate(), +now.getMonth() + 1, +now.getFullYear()];
      [dd, mm] = [ (dd < 10 ? '0' : '') + dd, (mm < 10 ? '0' : '') + mm ]
      return `${dd}.${mm}.${yyyy - 60},${dd}.${mm}.${yyyy}`
    }
  }
}
</script>

<style lang="scss">
$application-form-grid-gutter: 42px;
$application-form-input-grid-gutter: 11px;

.application-form {

  &__content {}

  .heading + & {
    margin-top: 45px;
  }

  .field {
    &__error {
      position: static;
    }
  }

  &__header {
    box-shadow: 0 3px 14px rgba(0, 0, 0, 0.05);
    padding: 0 0 49px;
  }

  &__container {}

  &__calculator {}

  &__body {
    padding: 21px 0 55px;
  }

  &__group {}

  &__group-inner {
    padding: 23px 0;
  }

  &__legend {
    color: #a0a0a0;
    font-size: 11px;
    font-weight: 700;
    letter-spacing: 1.5px;
    line-height: 1.6;
    margin-bottom: 20px;
    padding-left: 2px;
  }

  &__row {
    @include row($application-form-grid-gutter);
    margin-bottom: -21px;
  }

  &__col {
    @include col-ready($application-form-grid-gutter);
    @include col(1);
    margin-bottom: 21px;

    @for $i from 1 through $grid-columns - 1 {
      @for $d from $i + 1 through $grid-columns {
        @if $grid-columns % $d == 0 {
          &_#{$i}-#{$d} {
            @include col($i / $d);
          }
        }
      }
    }

    @include below(md) {
      @include col(1);
    }
  }

  &__input-group {
    @include row($application-form-input-grid-gutter);
    margin-bottom: -$application-form-input-grid-gutter;
  }

  //&__input-group &__col {
  //    @include col-ready($application-form-input-grid-gutter);
  //    margin-bottom: $application-form-input-grid-gutter;
  //
  //    .form-input__control,
  //    .form-input__label {
  //        padding-left: 13px;
  //        padding-right: 13px;
  //    }
  //}

  &__cell {
    @include col-ready($application-form-input-grid-gutter);
    @include col(1);
    margin-bottom: $application-form-input-grid-gutter;

    @for $i from 1 through $grid-columns - 1 {
      @for $d from $i + 1 through $grid-columns {
        @if $grid-columns % $d == 0 {
          &_#{$i}-#{$d} {
            @include col($i / $d);
          }
        }
      }
    }

    @include below(sm) {
      @include col(1);
    }

    .form-input__control,
    .form-input__label {
      padding-left: 13px;
      padding-right: 13px;
    }
  }

  &__bool {
    margin-top: 23px;
  }

  &__footer {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 45px $grid-gutter / -2 0;

    @include below(md) {
      flex-direction: column;
    }
  }

  &__checks {
    flex: 0 1 auto;
    margin: 0 $grid-gutter / 2;
    max-width: 400px;
  }

  &__check {
    & + & {
      margin-top: 28px;
    }
  }

  &__button-area {
    text-align: center;
    display: inline-flex;
    flex-direction: column;
    flex: 0 1 auto;
    margin: 0 $grid-gutter / 2;

    @include below(md) {
      margin-top: 50px;
    }
  }

  &__button {
    white-space: nowrap;
  }

  &__note {
    display: inline-block;
    margin-top: 16px;
    color: color(gray-light);
    font-size: 14px;
  }
}
</style>



