<template>
  <header class="header" :class="classes">
    <div class="header__container container-wide">
      <div class="header__content">
        <router-link to="/" class="header__logo">
          <icon name="logo" class="header__logo-img"></icon>
        </router-link>
        <div class="header__contacts">
          <contacts-inline></contacts-inline>
        </div>
        <div class="header__menu">
          <div class="header__menu-item u-hidden-xl-down">
            <portal to="info-link" :slim="true" :disabled="$mq.xlAndUp">
              <router-link to="/info" class="menu-link menu-link_info">
                <span class="menu-link__label">
                  <icon name="papers" class="menu-link__icon"></icon>
                </span>
                <span class="menu-link__text">Информация</span>
              </router-link>
            </portal>
          </div>
          <div class="header__menu-item u-hidden-md-down">
            <portal to="acct-link" :slim="true" :disabled="$mq.mdAndUp">
              <a class="menu-link menu-link_acct" href="#" @click.prevent="$root.$emit('show:auth')">
                <span class="menu-link__label">
                  <icon name="user" class="menu-link__icon"></icon>
                </span>
                <span class="menu-link__text">Личный кабинет</span>
              </a>
            </portal>
          </div>
        </div>
        <div class="header__toggle">
          <hamburger v-model="panelOpen"></hamburger>
        </div>
      </div>
    </div>
    <panel v-model="panelOpen" v-if="$mq.xlAndDown"></panel>
    <auth></auth>
  </header>
</template>

<script>
import mediaQueryMixin from '@/mixins/media-query'

export default {
  mixins: [mediaQueryMixin],
  data () {
    return {
      threshold: 40,
      isScatter: false,
      panelOpen: false
    }
  },
  computed: {
    classes () {
      return {
        'header_scatter': this.isScatter
      }
    }
  },
  methods: {
    onScroll () {
      this.isScatter = this.$mq.xlAndUp && window.pageYOffset < this.threshold
    }
  },
  created () {
    this.onScroll()
    window.addEventListener('scroll', this.onScroll)
  },
  destroyed () {
    window.removeEventListener('scroll', this.onScroll)
  }
}
</script>

<style lang="scss">
$header-transition-duration: 0.2s;
$header-transition: $header-transition-duration cubic-bezier(0, 0, 0.15, 1); // ease-out
$header-bp: xl;

.header {
  box-shadow: 0 3px 14px rgba(0, 0, 0, 0.05);
  background-color: #ffffff;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: z("sticky");

  &__container {}

  &__content {
    display: flex;
    align-items: center;
    justify-content: space-between;

    height: 72px;
    transition: height $header-transition;
  }

  @include above($header-bp) {
    &_scatter &__content {
      height: 57px;
    }
  }

  &__logo,
  &__menu {
    transform: none;
    flex: 0 0 auto;
    transition: transform $header-transition;
  }

  @include above($header-bp) {
    &_scatter &__logo,
    &_scatter &__menu {
      transform: translateY(84px);
    }

    &_scatter &__logo {}

    &_scatter &__menu {}
  }

  &__logo {}

  &__logo-img {
    transition: transform $header-transition, margin-top $header-transition;
    width: 183px;
    margin-top: -3px;
    transform-origin: left center 0;
    backface-visibility: hidden;
    font-size: 36px;
  }

  @include above($header-bp) {
    &_scatter &__logo-img {
      transform: scale(1.11);
      margin-top: 0;
    }
  }

  &__contacts {
    flex: 0 0 auto;
    transition: transform $header-transition;
  }

  @include above($header-bp) {
    &_scatter &__contacts {
      transform: translateX(40px);
    }
  }

  &__menu {
    display: flex;
    justify-content: flex-end;
  }

  &__menu-item {
    flex: none;

    &:not(:last-child) {
      margin-right: 40px;
    }
  }

  &__toggle {
    font-size: 0;
    margin-left: 10px;
    display: none;

    @include below(xl) {
      display: block;
    }
  }
}

.menu-link {
  display: inline-flex;
  align-items: center;
  vertical-align: middle;
  height: 35px;
  margin: 4px 0 3px 21px;
  border-radius: 9px;

  &_info {
    box-shadow: 0 0 16px rgba(0, 0, 0, 0.06);
    background-color: #ffffff;
    color: #000;
  }

  &_acct {
    box-shadow: 0 0 7px rgba(0, 0, 0, 0.12);
    background-color: color(primary);
    color: #fff;
  }

  &__label {
    display: inline-block;
    vertical-align: middle;
    background-color: inherit;
    border-radius: 50%;
    width: 42px;
    height: 42px;
    position: relative;
    margin-left: -21px;
    top: -0.5px;
    transition: 0.3s ease-out;
  }

  &:hover &__label {
    transform: scale(1.06);
  }

  &:active &__label {
    transform: scale(0.97);
  }

  &_info &__label {
    box-shadow: 0 0 13px rgba(0, 0, 0, 0.08);
  }

  &_acct &__label {
    box-shadow: 4px 0 13px rgba(0, 0, 0, 0.06);
  }

  &__icon {
    color: inherit;
    font-size: 21px;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
  }

  &_info &__icon {
    color: #363636;
  }

  &__text {
    display: inline-block;
    vertical-align: middle;
    line-height: 35px;
    font-size: 13px;
    white-space: nowrap;
    padding: 0 13px 0 7px;
  }
}
</style>



