<template>
  <div :class="classes">
    <slot></slot>
  </div>
</template>

<script>
export default {
  props: {
    stacked: Boolean
  },
  computed: {
    classes () {
      return [
        'checkbox-group',
        {
          'checkbox-group_stacked': this.stacked
        }
      ]
    }
  }
}
</script>

<style lang="scss">
.checkbox-group {
  margin: -10px 0;

  &_stacked {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }

  .checkbox {
    margin: 10px 20px 10px 0;
  }
}
</style>



