<template>
  <label :class="classes">
    <input
      class="checkbox__control"
      type="checkbox"
      v-bind="attributes"
      v-model="localChecked"
      :value="value"
      :true-value="value"
      :false-value="uncheckedValue"
      ref="input"
    />
    <span class="checkbox__box"></span>
    <span
      class="checkbox__label"
      v-if="label"
      v-html="label"
    ></span>
  </label>
</template>

<script>
import validationMixin from '@/mixins/validation'

export default {
  name: 'checkbox',
  $_veeValidate: {
    rejectsFalse: true
  },
  mixins: [validationMixin],
  inheritAttrs: false,
  inject: {
    Field: { default: null }
  },
  model: {
    prop: 'checked',
    event: 'change'
  },
  props: {
    checked: {},
    defaultChecked: Boolean,
    disabled: Boolean,
    indeterminate: Boolean,
    label: String,
    name: String,
    required: Boolean,
    uncheckedValue: {
      default: false
    },
    value: {
      default: true
    }
  },
  data () {
    return {
      localChecked: this.checked
    }
  },
  computed: {
    classes () {
      return [
        'checkbox'
      ]
    },
    attributes () {
      return {
        ...this.$attrs,
        disabled: this.disabled,
        name: this.name,
        required: this.required
      }
    }
  },
  watch: {
    checked (value) {
      this.localChecked = value
    },
    localChecked (value) {
      this.$emit('change', value)
      this.$emit('update:indeterminate', this.$refs.input.indeterminate)
    },
    indeterminate (value) {
      this.setIndeterminate(value)
    }
  },
  methods: {
    setIndeterminate (state) {
      this.$refs.input.indeterminate = state
      this.$emit('update:indeterminate', this.$refs.input.indeterminate)
    },
    focus () {
      this.$refs.input.focus()
    },
    blur () {
      this.$refs.input.blur()
    }
  },
  created () {
    if (this.defaultChecked) {
      const looseEqual = this._q
      const looseIndexOf = this._i
      if (Array.isArray(this.localChecked)) {
        let index = looseIndexOf(this.localChecked, this.value)
        index < 0 && this.localChecked.push(this.value)
      } else {
        if (!looseEqual(this.localChecked, this.value)) {
          this.localChecked = this.value
        }
      }
    }
    this.shouldValidate && this.customMessages({
      required: Array.isArray(this.localChecked)
        ? 'Необходимо отметить один или несколько пунктов'
        : 'Необходимо отметить данный пункт'
    })
  },
  mounted () {
    this.setIndeterminate(this.indeterminate)
  }
}
</script>

<style lang="scss">
$checkbox-transition: $form-input-transition;

.checkbox {
  display: inline-flex;
  align-items: baseline;
  cursor: pointer;
  user-select: none;

  &__control {
    @include visually-hidden;
  }

  &__box {
    display: inline-block;
    flex: none;
    width: 16px;
    height: 16px;
    border-radius: 3px;
    border: 1px solid #ececec;
    background-color: #ffffff;
    position: relative;
    top: 0.1em;

    &:before {
      content: '';
      display: block;
      width: 8px;
      height: 8px;
      border-radius: 2px;
      background-color: color(primary);
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      margin: auto;
      //transition: all $checkbox-transition;
      transform: scale(0);
    }
  }

  &__control:checked ~ &__box {
    &:before {
      transform: scale(1);
    }
  }

  &__control:indeterminate ~ &__box {
    &:before {
      transform: scale(1);
      //width: 8px;
      height: 2px;
      border-radius: 1px;
    }
  }

  &__control:focus ~ &__box {
    box-shadow: 0 0 0 1px #fff, 0 0 0 3px rgba(color(primary), 0.25);
  }

  &__label {
    display: inline-block;
    flex: 0 1 auto;
    color: #808080;
    line-height: 1.6;
    margin-left: 6px;
  }

  a {
    color: #b3b3b3;

    &:hover {
      color: inherit;
      text-decoration: underline;
    }
  }
}
</style>



