/* eslint-disable */

import { Validator } from 'vee-validate'

const dictionary = {
  name: 'ru',
  messages: {
    after: function (n, e) {
      return 'В поле должна быть дата после ' + e[0]
    },
    alpha_dash: function (n) {
      return 'Поле может содержать только буквы, цифры и дефис'
    },
    alpha_num: function (n) {
      return 'Поле может содержать только буквы и цифры'
    },
    alpha_spaces: function (n) {
      return 'Поле может содержать только буквы и пробелы'
    },
    alpha: function (n) {
      return 'Поле может содержать только буквы'
    },
    before: function (n, e) {
      return 'В поле должна быть дата до ' + e[0]
    },
    between: function (n, e) {
      return 'Поле должно быть между ' + e[0] + ' и ' + e[1]
    },
    confirmed: function (n, e) {
      return 'Поле не совпадает с ' + e[0]
    },
    credit_card: function (n) {
      return 'Поле должно быть действительным номером карты'
    },
    date_between: function (n, e) {
      return 'Поле должно быть между ' + e[0] + ' и ' + e[1]
    },
    date_format: function (n, e) {
      return 'Поле должно быть в формате ' + e[0]
    },
    decimal: function (n, e) {
      void 0 === e && (e = [])
      var t = e[0]
      return void 0 === t && (t = '*'), 'Поле должно быть числовым и может содержать ' + (t === '*' ? '' : t) + ' десятичных числа'
    },
    digits: function (n, e) {
      return 'Поле должно быть числовым и точно содержать ' + e[0] + ' цифры'
    },
    dimensions: function (n, e) {
      return 'Поле должно быть ' + e[0] + ' пикселей на ' + e[1] + ' пикселей'
    },
    email: function (n) {
      return 'Поле должно быть действительным электронным адресом'
    },
    ext: function (n, e) {
      return 'Поле должно быть действительным файлом. (' + e.slice(0) + ')'
    },
    image: function (n) {
      return 'Поле должно быть изображением'
    },
    in: function (n) {
      return 'Поле должно быть допустимым значением'
    },
    ip: function (n) {
      return 'Поле должно быть действительным IP-адресом'
    },
    max: function (n, e) {
      return 'Поле не может быть более ' + e[0] + ' символов'
    },
    max_value: function (n, e) {
      return 'Поле должно быть ' + e[0] + ' или менее'
    },
    mimes: function (n, e) {
      return 'Поле должно иметь действительный тип файла. (' + e.slice(0) + ')'
    },
    min: function (n, e) {
      return 'Поле должно быть не менее ' + e[0] + ' символов'
    },
    min_value: function (n, e) {
      return 'Поле должно быть ' + e[0] + ' или больше'
    },
    not_in: function (n) {
      return 'Поле должно быть допустимым значением'
    },
    numeric: function (n) {
      return 'Поле должно быть числом'
    },
    regex: function (n) {
      return 'Поле имеет ошибочный формат'
    },
    required: function (n) {
      return 'Поле обязательно для заполнения'
    },
    size: function (n, e) {
      return 'Поле должно быть меньше, чем ' + (function (n) {
        var e = (n = 1024 * Number(n)) == 0 ? 0 : Math.floor(Math.log(n) / Math.log(1024))
        return 1 * (n / Math.pow(1024, e)).toFixed(2) + ' ' + ['Byte', 'KB', 'MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB'][e]
      }(e[0]))
    },
    url: function (n) {
      return 'Поле имеет ошибочный формат URL'
    }
  },
  attributes: {}
}

Validator.localize('ru', dictionary)



