<!--<template>-->
  <!--<div class="step">-->
    <!--<div class="step__circle">-->
      <!--<icon :name="icon" class="step__icon"></icon>-->
    <!--</div>-->
    <!--<div class="step__desc" v-if="desc" v-html="desc"></div>-->
    <!--<div class="step__note" v-if="note" v-html="note"></div>-->
  <!--</div>-->
<!--</template>-->

<script>
export default {
  functional: true,
  props: {
    icon: {
      type: String,
      required: true
    },
    desc: {
      type: String
    },
    note: {
      type: String
    }
  },
  render (h, { props, data }) {
    return (
      <div class="step" {...data}>
        <div class="step__circle">
          <icon name={props.icon} class="step__icon"></icon>
        </div>
        <div class="step__desc" domPropsInnerHTML={props.desc}></div>
        <div class="step__note" domPropsInnerHTML={props.note}></div>
      </div>
    )
  }
}
</script>

<style lang="scss">
.step {
  width: 169px;
  text-align: center;

  &__circle {
    display: inline-block;
    width: 131px;
    margin-bottom: 25px;
    font-size: 0;
  }

  &__icon {
    display: inline-block;
    max-width: none;
    font-size: 131px;
  }

  &__desc {
    color: #292929;
    font-size: 18px;
    line-height: 20px;
  }

  &__note {
    color: color(gray-light);
    font-size: 14px;
    line-height: 16px;
    margin: 10px -6px 0;
  }
}
</style>



