<template>
  <div class="range">
    <div class="range__slider" ref="slider"></div>
    <template v-if="multiple">
      <input v-for="(item, index) in localValue" :key="index" type="hidden" :name="name && name + '[]'" :value="item">
    </template>
    <input v-else type="hidden" :name="name" :value="localValue">
    <div class="range__labels">
      <span class="range__label">{{ min | format }}</span>
      <span class="range__label">{{ max | format }}</span>
    </div>
  </div>
</template>

<script>
import noUiSlider from 'nouislider'

export default {
  props: {
    min: {
      type: Number,
      default: 0
    },
    max: {
      type: Number,
      default: 100
    },
    name: String,
    sliderOptions: {
      type: Object,
      default: () => ({})
    },
    step: {
      type: Number,
      default: 1
    },
    value: {
      type: [Number, Array],
      default: 50
    }
  },
  data () {
    return {
      localValue: this.value
    }
  },
  computed: {
    range () {
      return {
        min: [this.min],
        max: [this.max]
      }
    },
    multiple () {
      return Array.isArray(this.localValue) && this.localValue.length > 1
    }
  },
  watch: {
    value (value) {
      this.localValue = value
    },
    localValue (value) {
      this.$emit('input', value)
      this.setSliderValue(value)
    },
    range (value) {
      this.noUiSlider.updateOptions({ range: value })
    },
    step (value) {
      this.noUiSlider.updateOptions({ step: value })
    },
    sliderOptions: {
      handler () {
        this.updateSlider()
      },
      deep: true
    }
  },
  methods: {
    mountSlider () {
      if (this.noUiSlider) {
        return
      }

      this.noUiSlider = noUiSlider.create(this.$refs.slider, {
        animate: false,
        start: this.localValue,
        connect: this.multiple || 'lower',
        step: this.step,
        range: this.range,
        behaviour: 'snap',
        format: { to: Math.round, from: Math.round },
        ...this.sliderOptions
      })

      this.noUiSlider.on('update', (values, handle) => {
        this.localValue = this.multiple ? values : values[handle]
      })
    },
    destroySlider () {
      if (this.noUiSlider) {
        this.noUiSlider.destroy()
        this.noUiSlider = null
      }
    },
    updateSlider () {
      this.destroySlider()
      this.mountSlider()
    },
    setSliderValue (value) {
      const looseEqual = this._q
      const sliderValue = this.noUiSlider.get()
      const sliderValueLength = Array.isArray(sliderValue) ? sliderValue.length : 1
      const valueLength = Array.isArray(value) ? value.length : 1
      if (sliderValueLength !== valueLength) {
        this.updateSlider()
      } else if (!looseEqual(sliderValue, value)) {
        this.noUiSlider.set(value)
      }
    }
  },
  mounted () {
    this.mountSlider()
  },
  beforeDestroy () {
    this.destroySlider()
  },
  filters: {
    format (num) {
      return num.toString().replace(/(?!^)(\d{3})(?=(?:\d{3})*$)/g, ' $1')
    }
  }
}
</script>

<style lang="scss">
@import "~nouislider/distribute/nouislider";

.range {
  display: block;

  &__slider {
    display: block;
  }

  &__labels {
    display: flex;
    justify-content: space-between;
    margin-top: 6px;
  }

  &__label {
    display: inline-block;
    color: color(gray);
    font-size: 13px;
    line-height: 1;
  }

  .noUi-base {
    cursor: pointer;
  }

  .noUi-horizontal {
    height: 10px;
  }

  .noUi-target {
    background: #f7f7f7;
    border: none;
    border-radius: 0;
    box-shadow: none;
  }

  .noUi-connects {
    border-radius: 0;
  }

  .noUi-connect {
    background: color(primary);
    border-radius: 0;
    box-shadow: none;
  }

  .noUi-handle {
    border-radius: 50%;
    box-shadow: none;
    border: 6px solid rgba(178, 237, 166, 0.33);
    background: color(primary);
    background-clip: content-box;
    cursor: pointer;
    transition: transform 0.18s cubic-bezier(0.4, 0, 0.2, 1);
    transform: scale(0.95);

    &.noUi-active {
      transform: scale(1.1);
    }

    &:before,
    &:after {
      display: none;
    }
  }

  .noUi-horizontal .noUi-handle {
    width: 34px;
    height: 34px;
    left: -17px;
    top: -12px;
  }
}
</style>



