/* eslint-disable */

import { Validator } from 'vee-validate'

Validator.extend('phone', {
  getMessage (field, args) {
    return 'Поле должно быть в формате <nobr>+7 (123) 456-78-90</nobr>'
  },
  validate (value) {
    return value.length == 18 && /^\+\d\s\(\d{3}\)\s\d{3}-\d{2}-\d{2}$/.test(value)
  }
})

Validator.extend('sms_code', {
  getMessage (field, args) {
    return 'Поле имеет неверный формат'
  },
  validate (value) {
    return /^\d{4}$/.test(value)
  }
})

Validator.extend('age', {
  getMessage (field, args) {
    return 'Займы на нашем сайте выдаются от 21 до 70 лет'
  },
  validate (value) {
    let now = new Date(),
      [dd, mm, yyyy] = [+now.getDate(), +now.getMonth() + 1, +now.getFullYear()],
      [_dd, _mm, _yyyy] = value.split('.').map(Number),
      [age, m] = [yyyy - _yyyy, mm - _mm]

    if (m < 0 || (m === 0 && dd < _dd)) {
      age--
    }

    return age >= 21 && age <= 70
  }
})

Validator.extend('russian', {
  getMessage (field, args) {
    return 'Допускается ввод только русских букв'
  },
  validate (value) {
    return /^[а-яА-ЯёЁ0-9\- ]+$/.test(value)
  }
})

Validator.extend('capitalized', {
  getMessage (field, args) {
    return 'Первая буква должна быть заглавной'
  },
  validate (value) {
    return /^[А-ЯЁ ]+$/.test(value[0])
  }
})

Validator.extend('passport_number', {
  getMessage (field, args) {
    return 'Поле должно быть в формате 12 34 567890'
  },
  validate (value) {
    return /^\d{2} \d{2} \d{6}$/.test(value)
  }
})

Validator.extend('passport_sub_сode', {
  getMessage (field, args) {
    return 'Поле должно быть в формате 123-456'
  },
  validate (value) {
    return /^\d{3}-\d{3}$/.test(value)
  }
})



