<template>
  <div class="field" :class="classes">
    <label
      class="field__label"
      :for="id"
      v-if="label"
      v-html="label"
    ></label>
    <div class="field__content">
      <slot></slot>
      <icon name="check" class="field__check"></icon>
    </div>
    <div class="field__error"
         v-if="errorMessage"
         v-html="errorMessage"
    ></div>
  </div>
</template>

<script>
export default {
  provide () {
    return {
      Field: this
    }
  },
  props: {
    label: String
  },
  data () {
    return {
      disabled: false,
      errorMessage: null,
      hasError: false,
      id: null,
      isDirty: false,
      isFocused: false,
      isValid: false,
      maxlength: null,
      required: false,
      value: null,
      select: false,
      text: false
      // counter: null,
      // password: null,
      // togglePassword: false,
      // clear: false,
      // file: false
    }
  },
  computed: {
    classes () {
      return {
        'field_focused': this.isFocused,
        'field_dirty': this.isDirty,
        'field_error': this.hasError,
        'field_valid': this.isValid,
        'field_disabled': this.disabled,
        'field_required': this.required,
        'field_select': this.select,
        'field_text': this.text
      }
    }
  },
  methods: {}
}
</script>

<style lang="scss">
.field {
  position: relative;
  display: flex;
  flex-direction: column;
  font-size: 17px;

  &__label {
    @include text-truncate;
    align-self: flex-start;
    color: color(gray-light);
    transition: $form-input-transition;
    position: absolute;
    left: 1px;
    right: 1px;
    top: 0;
    bottom: 0;
    z-index: 10;
    padding: 0 23px;
    margin-top: 23px;
    line-height: 1;
    height: 1em;
    cursor: text;
    pointer-events: none;
  }

  &_dirty &__label,
  &_focused &__label {
    font-size: 12px;
    transform: translateY(-13px);
  }

  &__content {
    position: relative;
  }

  &__error {
    display: block;
    margin-top: 5px;
    color: color(danger);
    font-size: 12px;
    line-height: 1.5;
    padding-left: 2px;

    position: absolute;
    top: 100%;
    left: 0;
    right: 0;
  }

  &__check {
    position: absolute;
    top: 0;
    bottom: 0;
    right: 15px;
    margin-top: 25px;
    font-size: 13px;
    transform: translateY(3px);
    transition: 0.1s ease;
    opacity: 0;
    visibility: hidden;
  }

  //&_text#{&}_valid &__check,
  &_dirty#{&}_valid &__check {
    opacity: 1;
    visibility: visible;
  }

  &_select &__check {
    right: 40px;
  }
}
</style>



