<script>
export default {
  name: 'appdfdgf'
}
</script>

<style lang="scss">
@import "./assets/styles/main";

.app {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  max-width: 100%;
  overflow: hidden;
}

.main {
  flex: 1 0 auto;
  padding-top: 177px;

  @include below(xl) {
    padding-top: 140px;
  }
