<template functional>
  <div class="docs">
    <div class="docs__row">
      <ul class="docs__list">
        <li class="docs__list-item"
            v-for="([doc, link], index) in props.items.slice(0, 4)"
            :key="index"
        >
          <a :href="link">
            <icon name="pdf"></icon>
            {{ doc }}
          </a>
        </li>
      </ul>
      <icon name="contract" class="docs__img"></icon>
      <ul class="docs__list">
        <li class="docs__list-item"
            v-for="([doc, link], index) in props.items.slice(4)"
            :key="index"
        >
          <a :href="link">
            <icon name="pdf"></icon>
            {{ doc }}
          </a>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    items: {
      type: Array,
      default () {
        return [
          ['Правила предоставления займа', '#doc'],
          ['Заявление о предоставлении займа', '#doc'],
          ['Соглашение об использовании АСП', '#doc'],
          ['Заявление о предоставлении займа', '#doc'],
          ['Правила предоставления займа', '#doc'],
          ['Заявление о предоставлении займа', '#doc'],
          ['Соглашение об использовании АСП', '#doc'],
          ['Заявление о предоставлении займа', '#doc']
        ]
      }
    }
  }
}
</script>

<style lang="scss">
.docs {
  .title + & {
    margin-top: 60px;
  }

  &__row {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 0 $grid-gutter / -2;

    @include below(lg) {
      justify-content: space-around;
    }

    @include below(md) {
      margin: 0 $grid-gutter / -4;
    }

    @include below(sm) {
      flex-direction: column;
    }
  }

  &__list {
    flex: 0 1 auto;
    max-width: 345px;
    padding: 0 $grid-gutter / 2;

    @include below(md) {
      padding: 0 $grid-gutter / 4;
    }

    @include below(lg) {
      width: auto;
      max-width: none;
    }

    li {

      @include below(md) {
        margin-bottom: 25px;
      }

      a {
        display: flex;
        align-items: center;
        color: #181818;
        font-size: 18px;

        &:hover {
          color: color(primary);

          .icon {
            color: color(primary);
          }
        }

        .icon {
          color: #898989;
          margin-right: 15px;
        }
      }

      + li {
        margin-top: 37px;

        @include below(md) {
          margin-top: 0;
        }
      }
    }
  }

  &__img {
    position: relative;
    flex: none;
    margin: 0 $grid-gutter / 2;
    font-size: 131px;

    @include below(lg) {
      display: none;
    }
  }
}
</style>



