import Vue from 'vue'
import Router from 'vue-router'

import HomeView from '@/components/pages/HomeView.vue'
import InfoView from '@/components/pages/InfoView.vue'
import ApplicationFormView from '@/components/pages/ApplicationFormView.vue'
import ShoppingView from '@/components/pages/ShoppingView.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: HomeView
    },
    {
      path: '/info',
      name: 'info',
      component: InfoView
    },
    {
      path: '/application-form',
      name: 'application-form',
      component: ApplicationFormView
    },
    {
      path: '/shopping',
      name: 'shopping',
      component: ShoppingView
    },
    {
      path: '*',
      redirect: '/'
    }
  ],
  base: process.env.NODE_ENV === 'production' ? '/legkozaim/' : '',
  mode: 'history',
  // scrollBehavior: () => ({ y: 0 }),
  scrollBehavior (to, from, savedPosition) {
    if (savedPosition) {
      return savedPosition
    } else {
      return { x: 0, y: 0 }
    }
  }
})



