<template>
  <div class="account">
    <div class="account__header">
      <div class="account__title">Вы вошли как <b>Иванов Сергей Викторович</b></div>
    </div>
    <div class="account__body">
      <div class="account__debt">
        <div class="account__debt-label">Ваш долг на сегодня</div>
        <div class="account__debt-price">{{ debt }}  <small>₽</small></div>
      </div>
    </div>
    <div class="account__footer">
      <form class="account__form" novalidate action="#" @submit.prevent="onSubmit">
        <div class="account__field">
          <form-field :label="`${debt} <small>₽</small>`">
            <form-input name="payment" v-model="payment" v-validate="'required'"></form-input>
          </form-field>
        </div>
        <div class="account__action">
          <btn variant="primary" type="submit" class="account__button">ОПЛАТИТЬ</btn>
          <div class="account__cards">
            <icon name="visa" class="account__card" style="font-size: 13px"></icon>
            <icon name="master-card" class="account__card" style="font-size: 15px"></icon>
            <icon name="mir" class="account__card" style="font-size: 11px"></icon>
            <icon name="maestro" class="account__card" style="font-size: 15px"></icon>
          </div>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
export default {
  $_veeValidate: {
    validator: 'new'
  },
  props: {},
  data () {
    return {
      debt: 8400,
      payment: ''
    }
  },
  methods: {
    async onSubmit () {
      let result = await this.$validator.validateAll()
      if (result) {
        console.log(result)
      }
    }
  }
}
</script>

<style lang="scss">
$account-form-grid-gutter: 31px;

.account {
  color: #000000;

  &__header {
    text-align: center;
    margin-bottom: 69px;
  }

  &__title {
    font-size: 18px;
    line-height: 26px;
    font-weight: 400;
  }

  &__body {
    text-align: center;
    margin-bottom: 85px;
  }

  &__debt {}

  &__debt-label {
    color: color(gray);
    font-size: 18px;
    font-weight: 400;
    line-height: 26px;
    margin-bottom: 13px;
  }

  &__debt-price {
    font-size: 40px;
    font-weight: 700;
    line-height: 1;

    small {
      font-size: 65%;
    }
  }

  &__footer {}

  &__form {
    display: flex;
    align-items: center;
    margin: 0 $account-form-grid-gutter / -2 31px;

    @include below(sm) {
      flex-direction: column;
    }
  }

  &__field {
    flex: 1 1 auto;
    padding: 0 $account-form-grid-gutter / 2;
    position: relative;

    .form-error {
      position: absolute;
      top: 100%;
      left: 0;
      right: 0;
      margin-left: $account-form-grid-gutter / 2;
      margin-right: $account-form-grid-gutter / 2;
    }

    @include below(sm) {
      margin-bottom: 50px; //40
    }
  }

  &__action {
    flex: 1 1 auto;
    padding: 0 $account-form-grid-gutter / 2;
    position: relative;
  }

  &__button {}

  &__cards {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 10px;
    opacity: 0.7;
    position: absolute;
    top: 100%;
    left: 0;
    right: 0;

    img {
      flex: none;
      margin: 0 6px 12px;
    }
  }

  &__card {
    margin: 6px;
  }
}
</style>



