<script>
import { mergeData } from '@/utils'

export default {
  functional: true,
  props: {
    block: Boolean,
    disabled: Boolean,
    href: String,
    size: String,
    type: {
      type: String,
      default: 'button'
    },
    variant: {
      type: String,
      default: 'primary'
    }
  },
  render (h, { props, data, children }) {
    const componentData = {
      staticClass: 'btn',
      class: {
        ['btn_' + props.variant]: !!props.variant,
        ['btn_' + props.size]: !!props.size,
        'btn_block': props.block
      },
      attrs: {
        disabled: props.disabled,
        href: props.href,
        type: !props.href && props.type
      }
    }

    return h(props.href ? 'a' : 'button', mergeData(data, componentData), children)
  }
}
</script>

<style lang="scss">
.btn {
  @include button;
  border: none;
  max-width: 100%;
  transition: $transition;
  position: relative;
  line-height: 1.2;
  min-width: 228px;
  font-size: 20px;
  border-radius: 9px;
  padding: 21.5px 23px;

  &[disabled] {
    opacity: .65;
    pointer-events: none;
  }

  // sizes
  &_small {
    min-width: 0;
    font-size: 17px;
    border-radius: 4px;
    padding: 9px 18px;
  }

  &_large {}

  // variants
  &_primary {
    box-shadow: 0 15px 22px rgba(204, 204, 204, 0.43);
    background-color: color(primary);
    color: #ffffff;
    font-weight: 700;
    letter-spacing: 0.7px;
  }

  &_small#{&}_primary {
    box-shadow: 0 8px 11px rgba(204, 204, 204, 0.43);
    font-weight: 600;
    letter-spacing: 0;
  }

  &_secondary {}

  // display
  &_block {
    display: flex;
    width: 100%;
  }
}
</style>



