<template>
  <select
    class="input"
    v-bind="attributes"
    v-model="localValue"
    :multiple="multiple"
    ref="input"
    @change="onChange"
    @focus="onFocus"
    @blur="onBlur"
  >
    <slot name="first">
      <option value="" disabled v-html="placeholder"></option>
    </slot>
    <option
      v-for="(option, index) in options"
      :value="option.value"
      :disabled="option.disabled"
      :key="index"
      v-html="option.text"
    ></option>
    <slot></slot>
  </select>
</template>

<script>
import inputMixin from '@/mixins/input'
import validationMixin from '@/mixins/validation'

export default {
  mixins: [inputMixin, validationMixin],
  props: {
    multiple: Boolean,
    options: {
      type: [Array, Object],
      default: () => []
    },
    required: {
      type: Boolean,
      default: true
    }
  },
  data () {
    return {
      selectedText: null
    }
  },
  computed: {
    classes () {
      const { classes } = inputMixin.computed
      return {
        ...classes.call(this),
        'input_select': true
      }
    },
    isDirty () {
      const { multiple, localValue, placeholder, selectedText } = this
      return !!((multiple ? localValue.length : localValue) ||
          placeholder || selectedText)
    }
  },
  methods: {
    setFieldSelect (state) {
      this.Field.select = state
    },
    setDefaultSelected () {
      const { input } = this.$refs
      const selecteds = [].slice.call(
        input.querySelectorAll('option[selected]:not(:disabled)')
      )
      const looseIndexOf = this._i

      if (!selecteds.length) {
        return
      }

      if (!this.multiple) {
        const s = selecteds[0]
        this.localValue = '_value' in s ? s._value : s.value
      } else {
        this.localValue = selecteds.map((s) => {
          return '_value' in s ? s._value : s.value
        }).filter((v, i, t) => looseIndexOf(t, v) === i)
      }
    }
  },
  created () {
    this.setFieldSelect(true)
  },
  mounted () {
    this.setDefaultSelected()

    this.$watch('localValue', () => {
      const { options } = this.$refs.input
      this.$nextTick().then(() => {
        const sels = [].filter.call(options, o => o.selected)
        this.selectedText = sels.length ? sels[0].text : ''
      })
    }, { immediate: true })
  },
  beforeDestroy () {
    this.setFieldSelect(false)
  }
}
</script>



