<template>
  <div :class="classes">
    <div class="sector__container container">
      <slot></slot>
    </div>
  </div>
</template>

<script>
import bemMixin from '@/mixins/bem'

export default {
  block: 'sector',
  mixins: [bemMixin]
}
</script>

<style lang="scss">
.sector {
  box-shadow: 0 3px 14px rgba(0, 0, 0, 0.05);
  padding: 70px 0 105px; //45

  &__container {
    max-width: 916px + $site-padding * 2;
  }
}
</style>



