<template>
  <button :class="classes" type="button" @click.prevent="toggle"></button>
</template>

<script>
export default {
  model: {
    prop: 'active',
    event: 'change'
  },
  props: {
    active: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      isActive: this.active
    }
  },
  computed: {
    classes () {
      return [
        'hamburger',
        {
          'hamburger_active': this.isActive
        }
      ]
    }
  },
  watch: {
    active (value) {
      this.isActive = value
    },
    isActive (value) {
      this.$emit('change', value)
    }
  },
  methods: {
    toggle () {
      this.isActive = !this.isActive
    }
  }
}
</script>

<style lang="scss">
$hamburger-width: 24px;
$hamburger-height: 20px; //18
$hamburger-layer-thickness: 4px;
$hamburger-gradient-stop-1: $hamburger-height / 2 - $hamburger-layer-thickness / 2;
$hamburger-gradient-stop-2: $hamburger-gradient-stop-1 + $hamburger-layer-thickness;
$hamburger-color: #333;
$hamburger-transition: $popup-transition;
$hamburger-active-rotate: 180deg; //360
$hamburger-active-offset: 7px + 1;

.hamburger {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  width: $hamburger-width;
  height: $hamburger-height;
  background: linear-gradient(
      0deg, rgba($hamburger-color, 0),
      rgba($hamburger-color, 0) $hamburger-gradient-stop-1,
      $hamburger-color $hamburger-gradient-stop-1,
      $hamburger-color $hamburger-gradient-stop-2,
      rgba($hamburger-color, 0) $hamburger-gradient-stop-2,
      rgba($hamburger-color, 0) $hamburger-height
  );
  z-index: z("fixed") + 1;

  &:before,
  &:after {
    display: block;
    position: absolute;
    left: 0;
    width: 100%;
    height: $hamburger-layer-thickness;
    background-color: $hamburger-color;
    content: "";
  }

  &:before {
    top: 0;
    transform-origin: bottom left;
  }

  &:after {
    bottom: 0;
    transform-origin: top left;
  }

  &,
  &:before,
  &:after {
    transition: $hamburger-transition;
  }

  /* active states */
  &_active {
    transform: rotate($hamburger-active-rotate);
  }

  &_active:before {
    width: 75%;
    transform: translateY($hamburger-active-offset) rotate(-45deg);
  }

  &_active:after {
    width: 75%;
    transform: translateY(-$hamburger-active-offset) rotate(45deg);
  }
}
</style>



