<template>
  <textarea
    v-if="isTextarea"
    class="input"
    v-bind="attributes"
    v-model="localValue"
    :maxlength="maxlength"
    :placeholder="placeholder"
    :readonly="readonly"
    @change="onChange"
    @focus="onFocus"
    @blur="onBlur"
    ref="input"
  ></textarea>
  <input
    v-else
    :type="type"
    class="input"
    v-bind="attributes"
    v-model="localValue"
    :maxlength="maxlength"
    :placeholder="placeholder"
    :readonly="readonly"
    @change="onChange"
    @focus="onFocus"
    @blur="onBlur"
    ref="input"
  />
</template>

<script>
import Inputmask from 'inputmask/dist/inputmask/inputmask.date.extensions'
import inputMixin from '@/mixins/input'
import validationMixin from '@/mixins/validation'

export default {
  mixins: [inputMixin, validationMixin],
  props: {
    defaultValue: {},
    mask: [String, Object],
    maxlength: [String, Number],
    readonly: Boolean,
    type: {
      type: String,
      default: 'text'
    }
  },
  data () {
    return {}
  },
  computed: {
    isDirty () {
      return !!(this.localValue || this.placeholder ||
          ['time', 'date', 'datetime-local', 'week', 'month'].indexOf(this.type) > -1)
    },
    isTextarea () {
      return this.type === 'textarea'
    }
  },
  methods: {
    mountMask () {
      if (this.inputmask) {
        return
      }
      const input = this.$refs.input
      const maskOptions = typeof this.mask === 'string'
        ? { mask: this.mask }
        : this.mask

      this.inputmask = Inputmask({
        clearIncomplete: true,
        showMaskOnHover: false,
        ...maskOptions
      }).mask(input)

      // fix mask issue related with 'clearIncomplete' option
      input.addEventListener('incomplete', this.onInput)

      // make sure caret position always at the end of characters
      input.addEventListener('mousedown', (event) => {
        if (this.inputmask && !this.inputmask.isComplete()) {
          event.preventDefault()
          this.focus()
        }
      })
    },
    destroyMask () {
      if (this.inputmask) {
        this.inputmask.remove()
        this.inputmask = null
      }
    },
    shareTextInputData () {
      this.$watch('maxlength', (value) => {
        this.Field.maxlength = value
      }, { immediate: true })
    },
    setFieldText (state) {
      this.Field.text = state
    }
  },
  created () {
    if (this.defaultValue && !this.localValue) {
      this.localValue = this.defaultValue
    }

    this.setFieldText(true)
    this.shareTextInputData()
  },
  mounted () {
    if (this.mask) {
      this.mountMask()
    }

    if (this.localValue) {
      this.validate()
    }
  },
  beforeDestroy () {
    if (this.mask) {
      this.destroyMask()
    }

    this.setFieldText(false)
  }
}
</script>

<style lang="scss">
.input {
  width: 100%;
  padding: 29px 23px 10px;
  border-radius: 5px;
  border: 1px solid #ececec;
  background-color: #ffffff;
  font-size: 17px;
  transition: border $form-input-transition;
  @include placeholder {
    color: color(gray-light);
    font-weight: 300;
  }

  &_focused {
    border-color: rgba(94, 174, 224, 0.45);
  }

  &_error {
    border-color: color(danger);
  }

  &_select {
    background: #fff url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 4 5'%3E%3Cpath fill='%23666' d='M2 0L0 2h4zm0 5L0 3h4z'/%3E%3C/svg%3E") no-repeat right 16px center;
    background-size: 8px * 1.25 10px * 1.25;
    appearance: none;

    &:required:invalid {
      color: color(gray-light);
      font-weight: 300;
    }

    option {
      color: $body-color;

      &:disabled[value=""] {
        display: none;
      }
    }
  }
}
</style>



