<template>
  <div v-if="!isHidden">
    <transition
      enter-class="panel_out"
      enter-to-class=""
      enter-active-class=""
      leave-class=""
      leave-to-class="panel_out"
      leave-active-class=""
      @before-enter="onBeforeEnter"
      @enter="onEnter"
      @after-enter="onAfterEnter"
      @before-leave="onBeforeLeave"
      @leave="onLeave"
      @after-leave="onAfterLeave"
    >
      <div
        class="panel"
        tabindex="-1"
        ref="panel"
        v-show="isVisible"
        @keyup.esc="onEsc"
      >
        <div class="panel__inner">
          <div class="panel__header">
            <div class="panel__item u-hidden-md-up">
              <portal-target name="acct-link" slim></portal-target>
            </div>
            <div class="panel__item">
              <portal-target name="info-link" slim></portal-target>
            </div>
          </div>
          <div class="panel__footer">
            <div class="panel__item u-hidden-md-up">
              <portal-target name="phone-contact" slim></portal-target>
            </div>
            <div class="panel__item">
              <portal-target name="address-contact" slim></portal-target>
            </div>
          </div>
        </div>
      </div>
    </transition>
    <backdrop :visible="backdropVisible" @click="onClickOut"></backdrop>
  </div>
</template>

<script>
import popupMixin from '@/mixins/popup'

export default {
  mixins: [popupMixin],
  props: {
    lazy: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      eventKey: 'panel'
    }
  },
  methods: {
    onEnter () {
      popupMixin.methods.onEnter.call(this)
      this.$refs.panel.scrollTop = 0
    }
  },
  mounted () {
    this.$root.$on('show:panel', this.show)
    this.$root.$on('hide:panel', this.hide)
    if (this.visible) {
      this.show()
    }
  },
  beforeDestroy () {
    this.$root.$off('show:panel', this.show)
    this.$root.$off('hide:panel', this.hide)
  }
}
</script>

<style lang="scss">
$panel-transition: $popup-transition;

.panel {
  display: flex;
  flex-direction: column;
  background-color: #fff;
  position: fixed;
  top: 0;
  right: 0;
  overflow-y: auto;
  width: 300px;
  max-width: 100%;
  height: 100%;
  z-index: z("fixed");
  transition: transform $panel-transition;
  will-change: transform;
  box-shadow:
    0 6px 10px 0 rgba(0, 0, 0, 0.14),
    0 1px 18px 0 rgba(0, 0, 0, 0.12),
    0 3px 5px -1px rgba(0, 0, 0, 0.3);

  &_out {
    transform: translateX(100%);
  }

  &__inner {
    min-height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 90px 30px 30px 40px;
    flex: 1 0 auto;
  }

  &__header {}

  &__footer {
    margin-top: 80px;
  }

  &__item {
    margin-bottom: 30px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  & ~ .backdrop {
    z-index: z("fixed") - 1;
  }
}
</style>



