<script>
export default {
  functional: true,
  props: {
    title: {
      type: String
    },
    subtitle: {
      type: String
    }
  },
  render (h, { props, data }) {
    return (
      <div class="heading" {...data}>
        <h1 class="heading__title">{props.title}</h1>
        <div class="heading__subtitle">{props.subtitle}</div>
      </div>
    )
  }
}
</script>

<style lang="scss">
.heading {
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
  margin-bottom: 35px;

  &__title {
    color: color(gray-dark);
    font-size: 28px;
    line-height: 1.2;
    margin: 0 0 9px;

    @include below(md) {
      font-size: 27px;
    }
  }

  &__subtitle {
    color: color(gray);
    font-size: 20px;

    @include below(md) {
      font-size: 19px;
    }
  }
}
</style>



