<template>
  <transition
    enter-class=""
    enter-to-class=""
    enter-active-class=""
    leave-class=""
    leave-active-class=""
    leave-to-class=""
    @before-enter="onBeforeEnter"
    @enter="onEnter"
    @after-enter="onAfterEnter"
    @before-leave="onBeforeLeave"
    @leave="onLeave"
    @after-leave="onAfterLeave"
  >
    <div
      :is="tag"
      :id="id"
      :class="classes"
      v-show="isVisible"
    ><slot></slot></div>
  </transition>
</template>

<script>
export default {
  model: {
    prop: 'visible',
    event: 'change'
  },
  props: {
    id: String,
    accordion: String,
    visible: Boolean,
    tag: {
      type: String,
      default: 'div'
    }
  },
  data () {
    return {
      isVisible: this.visible,
      isTransitioning: false
    }
  },
  computed: {
    classes () {
      return {
        'collapsing': this.isTransitioning
      }
    }
  },
  watch: {
    visible (value) {
      this.isVisible = value
    },
    isVisible () {
      this.emitChange()
    }
  },
  methods: {
    toggle () {
      this.isVisible = !this.isVisible
    },
    onBeforeEnter (el) {
      this.isTransitioning = true
    },
    onEnter (el) {
      el.style.height = 0
      // eslint-disable-next-line
      el.offsetHeight
      el.style.height = el.scrollHeight + 'px'
      this.$emit('show')
    },
    onAfterEnter (el) {
      el.style.height = null
      this.isTransitioning = false
      this.$emit('shown')
    },
    onBeforeLeave (el) {
      this.isTransitioning = true
    },
    onLeave (el) {
      el.style.height = el.scrollHeight + 'px'
      // eslint-disable-next-line
      el.offsetHeight
      el.style.height = 0
      this.$emit('hide')
    },
    onAfterLeave (el) {
      el.style.height = null
      this.isTransitioning = false
      this.$emit('hidden')
    },
    emitChange () {
      this.$emit('change', this.isVisible)
      if (this.id) {
        this.$root.$emit(`collapse::change`, this.id, this.isVisible)
        if (this.accordion && this.isVisible) {
          this.$root.$emit(`collapse::accordion`, this.id, this.accordion)
        }
      }
    },
    toggleHandler (id) {
      if (id === this.id) {
        this.toggle()
      }
    },
    accordionHandler (id, accordion) {
      if (id !== this.id && accordion === this.accordion) {
        this.isVisible = false
      }
    }
  },
  mounted () {
    if (this.id) {
      this.$root.$on('toggle::collapse', this.toggleHandler)
      if (this.accordion) {
        this.$root.$on('collapse::accordion', this.accordionHandler)
      }
    }
  },
  beforeDestroy () {
    if (this.id) {
      this.$root.$off('toggle::collapse', this.toggleHandler)
      if (this.accordion) {
        this.$root.$off('collapse::accordion', this.accordionHandler)
      }
    }
  }
}
</script>

<style lang="scss">
.collapsing {
  overflow: hidden;
  transition: height 0.3s ease;
}
</style>



