const popups = []
const adjustSelectors = '.header'

export default {
  model: {
    prop: 'visible',
    event: 'change'
  },
  props: {
    noFade: Boolean,
    noCloseOnBackdrop: Boolean,
    noCloseOnEsc: Boolean,
    hideBackdrop: Boolean,
    visible: Boolean,
    lazy: {
      type: Boolean,
      default: true
    }
  },
  data () {
    return {
      isHidden: this.lazy || false,
      isVisible: false,
      isShow: false,
      isTransitioning: false,
      scrollbarWidth: 0,
      isBodyOverflowing: false
    }
  },
  computed: {
    adjustElements () {
      return [].slice.call(
        document.querySelectorAll(adjustSelectors)
      ).concat(document.body)
    },
    // backdropClasses() {
    //     return [
    //         'backdrop',
    //         {
    //             'backdrop_in': this.isShow
    //         }
    //     ];
    // },
    backdropVisible () {
      // let { hideBackdrop, isVisible, isTransitioning } = this;
      // return !hideBackdrop && (isVisible || isTransitioning);
      return !this.hideBackdrop && this.isVisible
    }
  },
  watch: {
    visible (value) {
      this[value ? 'show' : 'hide']()
    }
  },
  methods: {
    show () {
      if (this.isVisible) {
        return
      }
      this.emitEvent('show')
      this.isHidden = false
      this.$nextTick(() => {
        this.isVisible = true
        this.$emit('change', true)
      })
    },
    hide () {
      if (!this.isVisible) {
        return
      }
      this.emitEvent('hide')
      this.isVisible = false
      this.$emit('change', false)
    },
    onBeforeShow () {
      // console.log('onBeforeShow');
      if (popups.indexOf(this) === -1) {
        popups.push(this)
      }
      if (popups.length === 1) {
        this.checkScrollbar()
        this.setScrollbar()
        document.body.style.overflow = 'hidden'
      }
    },
    onBeforeEnter () {
      this.onBeforeShow()
      this.isTransitioning = true
    },
    onEnter () {
      // if (!this.hideBackdrop) {
      //     this.$refs.backdrop.offsetHeight;
      // }
      this.isShow = true
    },
    onAfterEnter () {
      this.isTransitioning = false
      this.$nextTick(() => {
        this.setFocus()
        this.emitEvent('shown')
      })
    },
    onBeforeLeave () {
      this.isTransitioning = true
    },
    onLeave () {
      this.isShow = false
    },
    onAfterHide () {
      popups.splice(popups.indexOf(this), 1)
      if (popups.length === 0) {
        this.resetScrollbar()
        document.body.style.overflow = ''
      }
    },
    onAfterLeave () {
      this.onAfterHide()
      this.isTransitioning = false
      this.$nextTick(() => {
        this.isHidden = this.lazy || false
        this.emitEvent('hidden')
      })
    },
    checkScrollbar () {
      const rect = document.body.getBoundingClientRect()
      this.isBodyOverflowing = rect.left + rect.right < window.innerWidth
    },
    getScrollbarWidth () {
      const scrollDiv = document.createElement('div')
      scrollDiv.style.cssText = `
                width: 100px; 
                height: 100px; 
                position: absolute; 
                overflow: scroll; 
                top: -9999px`
      document.body.appendChild(scrollDiv)
      this.scrollbarWidth = scrollDiv.getBoundingClientRect().width - scrollDiv.clientWidth
      document.body.removeChild(scrollDiv)
    },
    setScrollbar () {
      if (this.isBodyOverflowing) {
        this.adjustElements.forEach((el) => {
          const actualPadding = el.style.paddingRight
          const computedPadding = getComputedStyle(el).paddingRight
          el.setAttribute('data-padding-right', actualPadding)
          el.style.paddingRight = parseFloat(computedPadding) + this.scrollbarWidth + 'px'
        })
      }
    },
    resetScrollbar () {
      this.adjustElements.forEach((el) => {
        el.style.paddingRight = el.getAttribute('data-padding-right')
        el.removeAttribute('data-padding-right')
      })
    },
    emitEvent (event) {
      const e = {vueTarget: this}
      this.$emit(event, e)
      this.$root.$emit(`${this.eventKey}:${event}`, e)
    },
    onClickOut () {
      if (this.isVisible && !this.noCloseOnBackdrop) {
        this.hide()
      }
    },
    onEsc () {
      if (this.isVisible && !this.noCloseOnEsc) {
        this.hide()
      }
    },
    setFocus () {
      const active = document.activeElement
      const target = this.$el.querySelector('[tabindex]')
      if (target && !target.contains(active)) {
        target.focus()
      }
    }
  },
  mounted () {
    this.getScrollbarWidth()
  },
  beforeDestroy () {
    this.onAfterHide()
  }
}



