export default {
  props: {
    mods: {
      type: [String, Array]
    }
  },
  computed: {
    classes () {
      const block = this.$options.block || this.$options.name
      if (typeof block !== 'string') {
        return
      }
      const mods = this.mods ? [].concat(this.mods) : []
      return [
        block,
        ...mods.map(m => block + '_' + m)
      ]
    }
  }
}



